<template>
  <div>
    <v-form id="form" method="POST" :action="requestUrl" @submit.prevent="sendPhoto">
      <v-text-field label="URL изображения" box type="text" name="image_url" v-model="imageUrl"></v-text-field>
      <v-text-field label="ID фильтра" box type="text" name="template_name" v-model="templateName"></v-text-field>
      <v-btn type="submit">{{ loading }}</v-btn>
    </v-form>
    <v-img :src="processedImage" width="300"></v-img>
  </div>
</template>



<script>
import axios from "axios";
import { appendFile } from "fs";
export default {
  data() {
    return {
      requestUrl: "http://api-soft.photolab.me/template_process.php",
      imageUrl:
        "https://pp.userapi.com/c847122/v847122126/9f196/3dw4BFfO_GQ.jpg",
      templateName: 1668,
      loading: "Обработать изображения",
      processedImage:
        "https://pp.userapi.com/c847122/v847122126/9f196/3dw4BFfO_GQ.jpg"
    };
  },
  methods: {
    sendPhoto() {
      axios.defaults.preflightContinue = "true";
      let loading = "Обрабатывается";
      let formData = new FormData(document.getElementById("form"));
      axios
        .post(this.requestUrl, formData)
        .then(result => {
          console.log(result.data);
          this.processedImage = result.data;
        })
        .catch(err => {
          console.log(err);
        });
    }
  }
};
</script>

